document.addEventListener('DOMContentLoaded', function(){
    startApp();
});

function startApp(){
    navFija();
    createGalery();
    scrollNav();
}
function navFija(){
    const barra = document.querySelector('.header');
    //Es la seccion que sigue del header y sera la bandera para detectar y mantener fijo la barra de nsvegacion
    const sobreFestibal = document.querySelector('.about-festival');
    const body = document.querySelector('body');

    window.addEventListener('scroll', function(){
        if(sobreFestibal.getBoundingClientRect().top < 0 ){
            barra.classList.add('fijo');
            body.classList.add('body-scroll');
        }else{
            barra.classList.remove('fijo');
            body.classList.remove('body-scroll');
        }
    });

}

//Generar un scroll que recorra toda la web
function scrollNav(){
    const links = document.querySelectorAll('.principal-nav');
    links.forEach(enlace =>{
        enlace.addEventListener('click', function (e){
            e.preventDefault();

            const sectionScroll = e.target.attributes.href.value;
            const section = document.querySelector(sectionScroll);

            section.scrollIntoView({behavior: "smooth"});
        });
    });
}
function createGalery(){
    const galery = document.querySelector('.galeria-imagenes');

    for (let i = 1; i <= 12; i++) {
        const imagen = document.createElement('picture');
        
        imagen.innerHTML = 
        `
        <source srcset="build/img/thumb/${i}.avif" type="image/avif">
        <source srcset="build/img/thumb/${i}.webp" type="image/webp">
        
        <img loading="lazy" width="200" height="300" src="buil/img/thumb/${i}.jpg" alt="Imagen Vocalista">
        `;
        
        imagen.onclick = ()=>{
            showImage(i);
        }

        galery.appendChild(imagen);
    }
}

function showImage(id){
    console.log(id);

    const imagen = document.createElement('picture');
    imagen.innerHTML = 
        `
        <source srcset="build/img/grande/${id}.avif" type="image/avif">
        <source srcset="build/img/grande/${id}.webp" type="image/webp">
        
        <img loading="lazy" width="200" height="300" src="buil/img/grande/${id}.jpg" alt="Imagen Vocalista">
        `;

        //Crear el overlay que se sobrepone en toda la pantalla
        const overlay = document.createElement('DIV');
        const body = document.querySelector('body');

        overlay.appendChild(imagen);
        overlay.classList.add('overlay');

        //Eliminar haciendo click en cualquier parte del overlay
        overlay.onclick = () => {
            body.classList.remove('fijar-body');
            overlay.remove();
        }
        
        //crear el boton para destruir el overlay o modal
        const closePhoto = document.createElement('P');
        closePhoto.textContent = 'X';
        closePhoto.classList.add('btn-close');

        //Eliminar al hacer clic al boton
        closePhoto.onclick = () =>{
            body.classList.remove('fijar-body');
            overlay.remove();
        }

        overlay.appendChild(closePhoto);

        //a√±adir al html
        body.appendChild(overlay);
        //Elimiar scroll cuando se abra una imagen
        body.classList.add ('fijar-body');

}